<template>
  <div>
    <da-manage-page>
      <div slot="button">
        <el-button icon="my-zengjia" @click="dialogVisible=true;">新增</el-button>
      </div>
      <el-form slot="search" :inline="true" :model="searchForm.formdata">
        <!-- <el-form-item label="企业名称" v-show="entIdOptions.length > 1">
          <el-input v-model="searchForm.formdata.entId"></el-input>
        </el-form-item> -->

        <el-form-item label="产品名称">
          <el-input v-model="searchForm.formdata.name"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="getTbList">查询</el-button>
        </el-form-item>
      </el-form>
      <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

        <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
            <el-button size="small" @click="delTb(scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </da-table>
    </da-manage-page>

    <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')">
      <el-form label-width="90px" :model="addForm" :rules="rules" ref="addForm">
        <el-form-item label="企业名称" prop="entId" v-show="entIdOptions.length > 1">
          <el-select v-model="addForm.entId" filterable placeholder="请选择">
            <el-option v-for="item in entIdOptions" :key="item.dataValue" :label="item.dataText" :value="parseInt(item.dataValue)"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="产品名称" prop="name">
          <el-input auto-complete="off" v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="产品类型" prop="type">
          <!-- <el-input auto-complete="off" v-model="addForm.type"></el-input> -->
          <el-select v-model="addForm.type" filterable placeholder="请选择">
            <el-option v-for="item in typeOptions" :key="item.type" :label="item.typeName" :value="parseInt(item.type)"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="产品单位" prop="unit">
          <el-input auto-complete="off" v-model="addForm.unit"></el-input>
        </el-form-item>
        <el-form-item label="产品转换为标准品的系数" label-width="190px" prop="coefficient">
          <el-input auto-complete="off" v-model="addForm.coefficient"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
  </div>
</template>
<script>
export default {
  name: "product",
  data() {
    return {
      entIdOptions: [],
      typeOptions: [],
      // 查询表单
      searchForm: {
        formdata: { name: "" },
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头

        {
          label: "企业名称",
          prop: "entId",
          align: "left",
          hidden: false,
          sortable: true,
          displace: {}
        },
        { label: "产品名称", prop: "name", align: "left", sortable: true },
        { label: "产品类型", prop: "type", align: "left", sortable: true },
        { label: "产品单位", prop: "unit", align: "left", sortable: true },
        {
          label: "产品转换为标准品的系数",
          prop: "coefficient",
          align: "left",
          sortable: true
        }
      ],
      dialogVisible: false,
      addForm: { entId: "", name: "", type: "", unit: "", coefficient: "" },
      rules: this.eachValidate({
        entId: {
          required: {
            required: true,
            trigger: "change",
            message: "请选择企业"
          }
        },
        name: { required: true, maxString: "40" },
        type: {
          required: {
            required: true,
            trigger: "change",
            message: "请选择产品类型"
          }
        },
        unit: { maxString: "20" },
        coefficient: { min: 0 }
      })
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑产品资料管理" : "新增产品资料管理";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getTbList() {
      let formName = "table";
      this.$ajax
        .get("rest/product/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    getEntIdInfo() {
      this.$ajax.get("rest/product/entId/get").then(res => {
        if (res.data.status) {
          this.entIdOptions = res.data.records;
          if (res.data.records.length > 1) {
            let data = {};
            this.$each(res.data.records, item => {
              data[item.dataValue] = item.dataText;
            });
            this.colsData[0].displace = data;
          } else {
            this.colsData[0].hidden = true;
          }
        } else {
            this.colsData[0].hidden = true;
          }
      });
    },
    getEntType() {
      this.$ajax.get("rest/product/productType").then(res => {
        if (res.data.status) {
          this.typeOptions = res.data.records;
        }
      });
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = {
          entId: "",
          name: "",
          type: "",
          unit: "",
          coefficient: ""
        };
      });
    },
    saveDialog(formName) {
      if (this.entIdOptions.length === 1) {
        this.addForm.entId = this.entIdOptions[0].dataValue;
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/product", this.addForm).then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.dialogVisible = false;
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/product", { params: { id: row.id } })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    }
  },
  mounted() {
    this.getTbList();
    this.getEntIdInfo();
    this.getEntType();
  }
};
</script>



